import { Geometry } from "./geometry";
import { Face } from "./face";
import { SQRT_3 } from "../math/constants";
export class Triangle extends Geometry {
    constructor(a, b, c, color) {
        let verticies = [a, b, c];
        let faces = [new Face([0, 1, 2], verticies, color)];
        super(verticies, faces);
    }
}
export class EquilateralTriangle extends Triangle {
    constructor(base, color) {
        let height = base * (SQRT_3 / 2);
        super([0, 0, 0], [base / 2, height, 0], [base, 0, 0], color);
    }
}
