const square1TurnRegex = /(\/|\((-?\d),(-?\d)\))/g;
export function parseSquare1Algorithm(algorithm) {
    let turns = [];
    let match;
    while ((match = square1TurnRegex.exec(algorithm))) {
        if (match[0] === "/") {
            turns.push({ slice: true });
        }
        else {
            turns.push({
                top: parseInt(match[2]),
                bottom: parseInt(match[3]),
            });
        }
    }
    return turns;
}
